import React, { useState } from 'react';
import { Box, Button, TextField, Typography, Card, CardContent, LinearProgress } from '@mui/material';
import MusicNoteIcon from '@mui/icons-material/MusicNote';
import api from './api';

const ChiptuneGeneration = () => {
  const [prompt, setPrompt] = useState('');
  const [isGenerating, setIsGenerating] = useState(false);
  const [generatedMusic, setGeneratedMusic] = useState(null);
  const [error, setError] = useState('');

  const handleGenerate = async () => {
    if (!prompt.trim()) {
      setError('');
      return;
    }

    setIsGenerating(true);
    setError('');
    setGeneratedMusic(null);

    try {
      // APIChiptune
      const response = await api.post('/api/generate-chiptune', {
        prompt: prompt,
        bpm: 130  // BPM
      }, {
        responseType: 'blob'
      });

      // 
      const musicUrl = URL.createObjectURL(response.data);
      setGeneratedMusic(musicUrl);
    } catch (err) {
      setError(': ' + err.message);
      console.error(':', err);
    } finally {
      setIsGenerating(false);
    }
  };

  return (
    <Box sx={{ maxWidth: 800, margin: '0 auto', padding: 2 }}>
      <Typography variant="h4" gutterBottom>
        <MusicNoteIcon sx={{ verticalAlign: 'middle', marginRight: 1 }} />
        Chiptune
      </Typography>

      <Typography variant="body1">
        AIChiptune
        "8""Boss"
      </Typography>

      <Card sx={{ marginBottom: 3, backgroundColor: '#140424' }}>
        <CardContent>
          <TextField
            fullWidth
            label=""
            multiline
            rows={4}
            variant="outlined"
            value={prompt}
            onChange={(e) => setPrompt(e.target.value)}
            disabled={isGenerating}
            placeholder="8"
            sx={{
              mb :2,
              '& .MuiInputBase-input': { color: 'white' },
              '& .MuiInputLabel-root': { color: 'white' },
              '& .MuiOutlinedInput-root': {
                '& fieldset': {
                  borderColor: '#ff4500',
                },
                '&:hover fieldset': {
                  borderColor: '#ffa500',
                },
                '&.Mui-focused fieldset': {
                  borderColor: '#ffa500',
                },
              }
            }}
          />

          <Button
            variant="contained"
            color="primary"
            onClick={handleGenerate}
            disabled={isGenerating}
            sx={{
              background: 'linear-gradient(45deg, #ff4500, #ffa500)',
              color: '#333',
              fontWeight: 'bold',
              padding: '10px 20px',
              borderRadius: '50px'
            }}
          >
            {isGenerating ? '...' : ''}
          </Button>

          {isGenerating && (
            <LinearProgress sx={{ marginTop: 2 }} />
          )}

          {error && (
            <Typography color="error" sx={{ marginTop: 2 }}>
              {error}
            </Typography>
          )}
        </CardContent>
      </Card>

      {generatedMusic && (
            <Card sx={{ backgroundColor: '#140424' }}>
              <CardContent>
                <Typography variant="h6" gutterBottom sx={{ color: 'white' }}></Typography>
                <audio controls src={generatedMusic} style={{ width: '100%' }}>
                  
                </audio>
                <Button
                  variant="contained"
                  color="secondary"
                  onClick={() => {
                    const link = document.createElement('a');
                    link.href = generatedMusic;
                    link.download = 'generated_music.mp3';
                    document.body.appendChild(link);
                    link.click();
                    document.body.removeChild(link);
                  }}
                  sx={{
                    marginTop: 2,
                    background: 'linear-gradient(45deg, #1db954, #1ed760)',
                    color: 'white',
                    fontWeight: 'bold',
                    padding: '8px 16px',
                    borderRadius: '50px'
                  }}
                >
                  
                </Button>
              </CardContent>
            </Card>
          )}

      <Typography variant="h6" gutterBottom sx={{ marginTop: 4 }}>
        
      </Typography>
      <Typography variant="body1" component="div">
        <ul>
          <li></li>
          <li>Chiptune816</li>
          <li></li>
        </ul>
      </Typography>
    </Box>
  );
};

export default ChiptuneGeneration;